export const getDict = key => {
  var arr = dictArr[key]
  var dict = {}
  for (var i = 0; i < arr.length; i++) {
    dict[arr[i].value + ''] = arr[i].label
  }
  return dict
}

export const getDictArr = key => {
  return dictArr[key]
}

const dicts = { dictArr, getDict, getDictArr }

